.droppable-container {
  border: 2px dashed #ccc;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  margin: 20px;
  background: #f9f9f9;
  transition: border-color 0.3s;
  display: flex
;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dropped-active ,.drag-over{
  border-color: green;
}

.drag-and-drop-image {
  width: 60px;
  height: 60px;
}

.drag-and-drop-text {
  font-size: 20px;
  color: black;
}

.divider-container {
  display: flex;
  align-items: center;
  margin-top: 20px;
  width: 30%;
}

.divider {
  flex: 1;
}

.divider-text {
  margin: 0 10px;
  color: black;
}

.search-content-container {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.search-icon {
  width: 60px;
  height: 50px;
  margin-right: 10px;
}

.dropped-data {
  margin-top: 20px;
  padding: 10px;
  background: #e8ffe8;
  border: 1px solid #8bc34a;
  border-radius: 5px;
  text-align: left;
}
